@import '../../assets/scss/com.scss';
.ion{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    .icon_height{
        flex: 1;
        width: 100%;
        overflow-y: auto;
    }
    .ion_con{
        margin-top: 20px;
        width: 100%;
        background-color: #fff;
        padding: 0 30px;
        box-sizing: border-box;
        .con_fl{
            width: 100%;
           
            .fl_box{
                width: 100%;
                height: 80px;
                display: flex;
                align-items: flex-end;
                .box_one{
                    width: 44px;
                    height: 44px;
                    background: #BBBBBB;
                    border-radius: 50%;
                    font-size: 28px;
                    font-family: Source Han Sans CN;
                    font-weight: 400;
                    color: #FFFFFF;
                    text-align: center;
                    line-height: 44px;
                }
                .box_two{
                    width: 18px;
                    height: 100%;
                    margin-left: 30px;
                    margin-right: 30px;
                    display: flex;
                    align-items: flex-end;
                    justify-content: center;
                    padding-bottom: 12px;
                    box-sizing: border-box;
                    img{
                        width: 14px;
                        height: 14px;
                    }
                }
                .box_three{
                    height: 100%;
                    font-size: 28px;
                    font-family: Source Han Sans CN;
                    font-weight: 400;
                    display: flex;
                    align-items: flex-end;
                }
            }
        }
        .con_top{
            width: 100%;
            height: 90px;
            display: flex;
            align-items: center;
            font-size: 30px;
            font-family: Microsoft YaHei;
            font-weight: bold;
            color: #363636;
            border-bottom: 1px solid #B7B7B7;
            .top_line{
                width: 4px;
                height: 29px;
                background: $myBackColor;
            }
            .top_text{
                padding-left: 20px;
                box-sizing: border-box;
            }
        }
        
    }
    .con_fixed{
        // position: fixed;
        // left: 0;
        // bottom: 0;
        width: 100%;
        height: 98px;
        background: #FFFFFF;
        border-top: 1px solid #B7B7B7;
        font-size: 28px;
        font-family: Microsoft YaHei;
        font-weight: 400;
        color: #363636;
        line-height: 98px;
        text-align: center;
    }
}