.loading-container{
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index:999;
    display: flex;
    align-items: center;
    justify-content: center;
    .container_box{
      width: 300px;
      height: 270px;
      background-color: rgba($color: #000000, $alpha: 0.5);
      border-radius: 10px;
      .box_loading{
        width: 100%;
        display: flex;
        justify-content: center;
        .loading{
          height: 40px;
          margin: 0 auto;
          margin-top:100px;
          display: flex;
        }
        .loading span{
            display: inline-block;
            width: 10px;
            height: 100%;
            border-radius: 4px;
            background: lightgreen;
            animation: load 1s ease infinite;
        }
        .loading span:nth-child(2){
          animation-delay:0.2s;
        }
        .loading span:nth-child(3){
            animation-delay:0.4s;
        }
        .loading span:nth-child(4){
            animation-delay:0.6s;
        }
        .loading span:nth-child(5){
            animation-delay:0.8s;
        }
      }
    }
  }
@keyframes load{
    0%,100%{
        height: 40px;
        background: lightgreen;
    }
    50%{
        height: 70px;
        margin: -15px 0;
        background: lightblue;
    }
}
